<template>
  <div>
    <vxe-grid
      border
      stripe
      resizable
      show-overflow
      class="mb-10"
      :loading="loading"
      :scroll-x="{ enabled: false }"
      ref="xTable"
      :columns="tableColumn"
      :data="tableData"
      :tooltip-config="{
        showAll: true,
        contentMethod: showTooltipMethod,
      }"
      @header-cell-dblclick="headerCellDBLClickEvent"
      @cell-dblclick="cellDBLClickEvent"
    ></vxe-grid>
  </div>
</template>

<script>
import Vue from "vue";

import "xe-utils";
import XEUtils from "xe-utils";
import VXETable from "vxe-table";
import "vxe-table/lib/style.css";
Vue.use(VXETable);

export default {
  props: ["dataset", "trans_type", "metric"],
  data() {
    var result_json = [];
    try {
      result_json = require("../../../../../public/Tasks/" +
        this.$store.state.taskinfo.name +
        "/results/" +
        this.dataset +
        "/" +
        this.trans_type +
        "_" +
        this.metric +
        ".json");
    } catch (err) {
      // console.log(err);
      // result_json_list = "No data";
      result_json = [{ attack_results: {} }];
    }

    var table_column = new Array();
    // var table_data = new Array();
    var grid_width = 160;
    table_column.push({
      title: "Model/" + this.metric,
      field: "model",
      width: 185,
      // fixed: "left",
      showOverflow: true,
      titleHelp: {
        message: "Double click model or method name to get more information",
        icon: "vxe-icon--info",
      },
    });
    var col_array = new Array();
    for (var item of result_json) {
      for (var result_name in item.attack_results) {
        var result_info = item.attack_results[result_name];
        var title = "";
        if (result_info.sample_num != undefined) {
          title = result_name + "(" + result_info.sample_num + " samples )";
        } else {
          title = result_name;
        }
        if (col_array.indexOf(title) != -1) {
          continue;
        } else {
          col_array.push(title);
        }
        if (this.trans_type == "subpopulation") {
          table_column.push({
            title: title,
            children: [
              {
                field: "attack_results." + result_name + ".trans",
                title: "trans",
                minWidth: grid_width,
                sortable: true,
                formatter: this.formatterNum,
              },
            ],
          });
        } else {
          table_column.push({
            title: title,
            children: [
              {
                field: "attack_results." + result_name + ".ori",
                title: "ori",
                width: grid_width,
                sortable: true,
                formatter: this.formatterNum,
              },
              {
                field: "attack_results." + result_name + ".trans",
                title: "trans",
                width: grid_width,
                sortable: true,
                formatter: this.formatterNum,
              },
            ],
          });
        }
      }
    }

    return {
      loading: false,
      tableData: [],
      result_json: result_json,
      tableColumn: table_column,
    };
  },
  created() {
    this.loading = true;
  },
  mounted() {
    this.loadList();
  },
  methods: {
    loadList() {
      // this.loading = true;
      this.mockList().then((data) => {
        const xTable = this.$refs.xTable;
        // const startTime = Date.now();
        var wait_time = {
          domain: 0,
          ut: 250,
          domain_domain: 500,
          domain_ut: 1000,
          ut_ut: 2000,
        };
        if (xTable) {
          setTimeout(() => {
            xTable.reloadData(data).then(() => {
              // console.log(`用时 ${Date.now() - startTime}毫秒`);
              this.loading = false;
            });
          }, wait_time[this.trans_type]);
        }
      });
    },
    mockList() {
      return new Promise((resolve) => {
        resolve(this.result_json);
      });
    },
    formatterNum({ cellValue }) {
      if (cellValue == "null") {
        cellValue = "\\";
        return cellValue;
      } else return XEUtils.commafy(XEUtils.toNumber(cellValue), { digits: 2 });
    },
    headerCellDBLClickEvent({ column }) {
      if (
        column.title == "ori" ||
        column.title == "trans" ||
        column.title.indexOf("Model") != -1
      ) {
        return;
      }
      let _self = this;
      let routerUrl = this.$router.resolve({
        path: "/chartdisplay",
        query: {
          task: _self.$store.state.taskinfo.name,
          name: column.title.split("(")[0],
          display_type: "transformations",
          dataset: this.dataset,
          metric: this.metric,
          trans_type: this.trans_type,
        },
      });
      window.open(routerUrl.href, "_blank");
    },

    cellDBLClickEvent({ column, row }) {
      if (column.level == 1) {
        let _self = this;

        let routerUrl = this.$router.resolve({
          path: "/chartdisplay",
          query: {
            task: _self.$store.state.taskinfo.name,
            name: row.model,
            display_type: "models",
            dataset: this.dataset,
            metric: this.metric,
            trans_type: this.trans_type,
          },
        });
        window.open(routerUrl.href, "_blank");
      }
    },

    showTooltipMethod({ type, column, row, _columnIndex }) {
      const { property } = column;
      if (type === "header" && _columnIndex === -1) {
        return column.title
          ? "Doble click to display the result of " + column.title + " in chart"
          : "";
      } else if (type === "body" && _columnIndex === 0) {
        return row[property]
          ? "Doble click to display the result of " + row.model + " in chart"
          : "";
      } else return null;
    },
  },
};
</script>
